@import 'react-big-calendar/lib/sass/styles';
@import 'react-big-calendar/lib/addons/dragAndDrop/styles';

.site-layout-content {
    background: #fff;
    padding: 24px;
    min-height: 280px;
}
#components-layout-demo-top .logo {
    width: 120px;
    height: 31px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px 24px 16px 0;
    float: left;
}

.main-layout_all-height {
    height: 100%;
}

.main-container {
    height: 100vh;
    margin-top: 62px;
}

.header-block {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    & .header-block__title-page {
        margin-bottom: 0;
        color: #fff;
    }
}